@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body, html{
  background-color: rgb(6, 10, 14);
  -ms-scroll-chaining: none;
      overscroll-behavior: none;
}


@-webkit-keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.container{
  width: 100%;
  padding: 0;
  margin: 0;
}
.main-screen{
  height: 100vh;
  background-attachment: fixed;
  background-image:  url(/static/media/image-earth.ae8ebf5a.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}


::-webkit-scrollbar {
  width: 0px;

}

#particle-canvas {
  position:fixed !important;
  left:0;
  top:0;
  width:100%;
  height:100%;
}
.container-home{

    font-Family: "Roboto Condensed";
    height: 190vh;
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
}
#upper-home-screen{
    height: 90vh;
    padding: 0;
    margin: 0;
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
}
#upper-home-screen div{
    position: relative;
    top: 5vh;
}
#upper-home-screen h1, #upper-home-screen h2{
    color: white;
    position: relative;


}
#upper-home-screen h1{
    text-shadow: 5px 5px 4px rgba(0, 0, 0, 0.438);
    font-size: 3.3vw;
    padding-bottom: 15px;
    opacity: 0;
    line-height: 20px;
    letter-spacing: 11px;
    -webkit-animation: appear 4s forwards;
            animation: appear 4s forwards;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;

}
#upper-home-screen h2{
    text-shadow: 6px 6px 4px rgba(0, 0, 0, 0.538);
    font-size: 2.5vw;
    font-weight: 100;
    letter-spacing: 10px;
    opacity: 0;
    -webkit-animation: appear 2s forwards;
            animation: appear 2s forwards;
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
}

#upper-home-screen button{
    font-family: 'Roboto Condensed';
    text-shadow: 5px 4px 4px rgba(0, 0, 0, 0.534);
    font-weight: 100;
    font-size: 1.2vw;
    letter-spacing: 0.2vw; 
    color: #FFD0A5;
    width: 17vw;
    height: 6vh;
    display: block;
    margin: 0;
    padding: 0;
    background: none;
    border-color: #FFD0A5;
    box-Shadow: 2px 2px 4px rgb(0 0 0/ 0.25);
    border-radius: 40px;
    transition: all 0.3s ease-out;
}

#upper-home-screen button:hover{
    background: #FFD0A5;
    color: black;

}

#icons{
    width: 50vw;
    display:flex;
    justify-content: flex-end;
    flex-direction: row;
    position: absolute;
    bottom: -95vh;
    right: 10vw;
}

#icons a{
    padding-left: 50px;
    width: 4vh;
    height: 4vh;
    opacity: 0.7;
}

@media(max-width: 600px){
    .container-home{
    height: 290vh;
    }

    #icons{
        bottom: -195vh;
    }

    #upper-home-screen button{
        width: 40vw;
        font-size: 3.5vw;
    }

    #upper-home-screen h1, #upper-home-screen h2{
        letter-spacing: 0.1vw;
        font-size: 6vw;
        width: 65vw;
    }

}
.lower-home-screen{
    background-color: white;
    padding: 0;
    margin: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: row;
}
.lower-home-screen .picture{
    width: 30vw;
    height: 100vh;
    background-attachment: fixed;
    background-image: url(/static/media/image-mars.05e7cfdd.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    line-height: 10vh;
    color: white;
}

.picture h1{
    font-size: 2.6vw;
    padding-top: 0;
    margin-top: 0;
    line-height: 0px;
    font-weight: Bold;
    letter-spacing: 6vw;
    position: relative;
    left: 2vw;
}
.picture #destination{
    line-height: 10vh;
}
.picture p{
    padding-bottom: 0;
    font-size: 1vw;
    margin-bottom: 0;
    line-height: 15vh;
    font-weight: 300;
    letter-spacing: 4px;
    position: relative;
    left: 2vw;
    opacity: 0.9;
}

.content{
    width: 70vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
#right {
    color:rgb(255, 255, 255);
}



.content h1{
    position: relative;
    left: -9vw;
    letter-spacing: 20px;
    font-size: 3.1vw;
}

.content p{
    margin-top: 0;
    position: relative;
    left: 9vw;
    font-size: 1vw;
    font-weight: 100;
    letter-spacing: 0.1vw;
    line-height: 40px;
    width: 25vw;
}

#right p{
    width: 44vw;
    left: 5vw;
}

.content a{
    text-decoration: none;
    color: #FFD0A5;
    text-shadow: 1px 1px  1px black;
    font-weight: 500;
}
.content hr{
    width: 15vw;
    color: black;
    position: relative;
    left: -13vw;
    padding: 0;
    margin: 0;

}

@media(max-width: 600px){

    .lower-home-screen{
        flex-direction: column;
        height: auto;
    }
    #right-screen{
        flex-direction: column-reverse;
    }

    .lower-home-screen .picture{
        width: 100vw;
        height: 100vh;
        background-repeat: round;
    }


    .content{
        height: 100vh;
        width: 100vw;
    }
    
    .content #right{
        height: 100vh;
    }
    #right h1{
        padding-bottom: 2vh;
    }
    #right p{
        font-size: 3vw;
        line-height: 3.5vh;
        left: -5vw;
        width: 75vw;
        margin-top: 1vh;
    }

    .content p{
        font-size: 3.5vw;
        width: 65vw;
    }
    .content h1{
        font-size: 6vw;
        letter-spacing: 2vw;
    }

    .picture h1{
        font-size: 10vw;
        letter-spacing: 17vw;

    }
    .picture #destination{
        font-size: 3vw;
    }
    .picture p{
        font-size: 3vw;
    }

    .content hr{
        width: 50vw;
    }
}
#news{
    position: absolute;
    height: calc(100% - 80vh);
    width: 100%;
    z-index: 0;
    bottom: 0;
    background-color: rgb(6, 10, 14);
}

#aboutus{
    position: absolute;
    height: 100vh;
    top:200vh;
    width: 70vw;
    z-index: 0;
    bottom: 0;
    background-color: rgb(25, 27, 41);
}

@media(max-width: 600px){
    #aboutus{
        width: 100vw;
        top: 400vh;
    }
    
}
