.container-subpage{
    font-family: "Roboto Condensed";
    height: 90vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
#faq-buttons-container{
    display:flex;
    flex-direction: column;
    justify-content: center;
}
#faq-buttons-container hr{
    color:white;
    width: 0;
    height: 6vh;
    opacity: 0.5;
}

#faq-buttons-container img{
    height: 3.8vh;
    -webkit-filter: invert(100%) sepia(0%) saturate(7480%) hue-rotate(256deg) brightness(100%) contrast(100%);
            filter: invert(100%) sepia(0%) saturate(7480%) hue-rotate(256deg) brightness(100%) contrast(100%);
}

